.btn {
  min-width: 240px;
  min-height: 72px; 
  border-radius: var(--border-radius-sm);
  padding: 24px 48px;
  text-align: center;
  @include text(21px, var(--clr-light), 114%, 700);
  @include hover {
    filter: brightness(90%);
  }
  &:focus-visible {
    outline: 4px solid var(--clr-accent-10);
  }
}

.btn_primary {
  background-color: var(--clr-accent);
}

.btn_dark {
  background-color: var(--clr-dark);
}

.btn_success {
  background-color: var(--clr-success);
}

.btn_outlined {
  background-color: transparent;
  color: var(--clr-dark);
  outline: var(--border);
  outline-offset: -4px;
}

.btn_light {
  background-color: var(--clr-dark-2);
  color: var(--clr-dark-75);
}

@include respond(medium) {
  .btn {
    font-size: 18px;
  }
}

@include respond(medium) {
  .btn {
    font-size: 18px;
  }
}

@include respond(mobile) {
  .btn {
    max-width: 100%;
  }
}