.footer {
  .flex-container {
    flex-wrap: wrap;
  }

  padding: 64px 0;
  border-top: 4px solid var(--clr-dark-5);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  &__list {
    @include flex-h-center;
    flex-wrap: wrap;
    row-gap: 32px;
    column-gap: 48px;
  }

  &__item {
    @include text(21px, var(--clr-dark-75), 171%, 500);
    
    .footer__link {
      @include hover {
        color: var(--clr-accent);
      }
    }
  }
}

@include respond(tablet) {
  .footer {
    &__list, .media {
      margin: 0 auto;
    }

  }
}

@include respond(mobile) {
  .footer {
    .media {
      flex-wrap: wrap;
      row-gap: 16px;
      align-items: center !important;

     .divider {
      display: none;
     }

     .link_colored {
      margin-right: 0;
     }
    }

  }
}