.feedback {
  @include flex-v-center;
  column-gap: 24px;
  margin-top: var(--content-offset);
  overflow: hidden;

  .content {
    @include flex-all-sb;
    position: absolute;
    transition: 0.6s ease-in-out;
    opacity: 0;
    transform: translateX(200%);

    column-gap: 48px;

    .link_colored {
      margin-top: 8px;
    }
    
    .text {
      margin-top: 32px;
    }

    .text-content {
      max-width: 670px;
    }

    &__img {
      max-width: 640px;
      max-height: 640px;
      object-fit: cover;

      img {
        border-radius: var(--border-radius-md);
      }
    }
  }

  .content.active {
    transform: translateX(0);
    opacity: 1;
    transition: 0.6s ease-in-out;
    position: static;
  }
}


.bullets > * + *{
  margin-top: 8px;
}

.arrow {
  width: 16px;
  height: 8px;
}


#up-arrow {
  transform: rotate(180deg);
}

.tab {
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 100vh;
  background-color: var(--clr-accent-10);
  transition: 0.3s;
}

.tab.active {
  background-color: var(--clr-accent);
  height: 48px;
  transition: 0.3s;
}