.media {
  @include flex-all-center;
  column-gap: 48px;
  position: relative;

  .link_colored {
    margin-right: 48px;
  }
  
  .divider {
    width: 2px;
    height: 100%;
    background-color: var(--clr-dark-5);
    border-radius: 100vh;
    position: absolute;
    top: 50%;
    left: 55%;
    transform: translate(-50%, -50%);
  }
}

