.skills {
  margin-top: var(--content-offset);
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
}
.skill {
  @include hover {
    transform: scale(1.03);
  }
  cursor: pointer;
  flex: 1 1 auto;
  padding: 24px;
  @include flex-all-center;
  border: var(--border-light);
  border-radius: var(--border-radius-sm);
}

