.section-courses {
  .tabs {
    @include flex-all-center;
    column-gap: 32px;
  }

  .courses {
    position: relative;
    
    &__list {
      display: grid;
      position: absolute;
      opacity: 0;
      transform: translateX(40%);
    }
  }
}

.row {
  margin-top: var(--content-offset);
}

.courses {
  margin-top: 48px;
  position: relative;
  
	&__item {
    @include flex-all-center;
    @include hover {
      transform: scale(1.02);
    }

    border-radius: var(--border-radius-md);
    padding: 64px;
    cursor: pointer;

    .fourth-heading {
      margin-top: 32px;
    }

    &_btn {
      @include flex-all-center;
      border: var(--border);

      .fourth-heading {
        margin-top: 0;
      }
    }
	}

	&__icon {
    @include flex-all-center;
    width: 100px;
    height: 100px;
    border-radius: var(--border-radius-md);
    margin: 0 auto;
	}

  &__list {
    display: grid;

    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    grid-auto-rows: 305px;
    gap: 32px;
  }

  &__list.active {
    opacity: 1;
    transform: translateX(0);
    transition: 0.5s;
    position: static;
  }

}


@each $key, $value in $colors {
  .courses {
    &__item_#{$key} {
      background-color: rgba($value, 0.1);

      .courses__icon {
          background-color: $value;
      }

      .fourth-heading {
        color: $value;
      }
     }

  }
}