.overlay {
  position: fixed;
  z-index: 100;
  inset: 0;
  background-color:rgba(0, 20, 32, 0.25);
  height: 100vh;
  min-width: 100%;
  opacity: 0;
  transition: 0.5s;
  left: -1000%;
  visibility: hidden;
}

.overlay.active {
  left: 0;
  opacity: 1;
  visibility: visible;
  transition: 0.3s;
}

.modal {
  @include flex-all-sb;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  column-gap: 96px;
  width: 70%;
  padding: 96px;
  border-radius: var(--border-radius-md);
  background-color: var(--clr-light);

  .form {
    margin-top: 48px;
  }

  .icon {
    @include hover {
      transform: scale(1.03);
    }
    position: absolute;
    top: 24px;
    right: 24px;
    svg {
      width: 24px;
      height: 24px;
    }
  }

  .img {
    @include hover {
      transform: scale(1.04);
    }
    cursor: pointer;
  }
}

.modal.active {
  display: flex;
}

.modal.success {
  transition: 0.3s;
  background-color: #EAFAE9;
}

