.results {
  margin-top: var(--content-offset);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 320px);
  gap: 64px;

  .img {
    @include hover {
      transform: scale(1.04);
    }
    cursor: pointer;
  }

}
.result {
  @include flex-v-center;
  column-gap: 64px;

  .fourth-heading {
    max-width: 430px;
  }
}
