.section-course {
  .text {
    margin-top: 48px;
  }

  .text-content {
    max-width: 1060px;
  }

  .head {
    @include flex-all-sb;
    flex-wrap: wrap;
    gap: 32px;
  }
  
  .courses {
    margin-top: var(--content-offset);

    &__list {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
      grid-auto-rows: 305px;
      gap: 32px;
    }
  }
}





