:root {
  --clr-accent: #009dff;
  --clr-accent-10: #e6f6ff;
  --clr-gradient: linear-gradient(90deg, #00bffe 0%, #017eff 99.99%);
  --clr-secondary: #ff8928;
  --clr-light: #fff;
  --clr-dark: #001420;
  --clr-dark-75: #404b51;
  --clr-dark-50: #80878b;
  --clr-dark-25: #bfc3c5;
  --clr-dark-15: #d9dbdc;
  --clr-dark-5: #f2f3f3;
  --clr-dark-2: #fafafa;
  --clr-success: #32c538;
  --clr-danger: #ff4242;
  --font-family: "Manrope", sans-serif;

  --border: 4px solid var(--clr-dark);
  --border-light: 4px solid var(--clr-dark-5);
  --border-error: 4px solid var(--clr-danger);
  --border-accent: 4px solid var(--clr-accent);

  --border-radius-sm: 8px;
  --border-radius-md: 16px;

  --content-width: 1680px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --section-offset: 120px;
  --content-offset: 80px;
}

$headers-range: (
  first: (
    size: 88px,
    color: var(--clr-light),
    line-height: 109%,
  ),
  second: (
    size: 66px,
    color: var(--clr-dark),
    line-height: 120%,
  ),
  third: (
    size: 48px,
    color: var(--clr-dark),
    line-height: 125%,
  ),
  fourth: (
    size: 36px,
    color: var(--clr-dark),
    line-height: 125%,
  ),
  fifth: (
    size: 28px,
    color: var(--clr-dark),
    line-height: 115%,
  ),
  sixth: (
    size: 21px,
    color: var(--clr-dark),
    line-height: 114%,
  ),
);

$colors: (
  "yellow": #ffca43,
  "blue": #0085ff,
  "red": #ff4343,
  "purple": #7443ff,
  "pink": #ff43b4,
  "green": #2dcb1f,
  "orange": #ff9d43,
);

$breakpoint-xsmall: 320px;
$breakpoint-small: 576px;
$breakpoint-medium: 768px;
$breakpoint-large: 1128px;
$breakpoint-extra-large: 1300px;

