.section-test {  
  margin-top: 96px;

  .text.text-center {
    margin-top: 32px;
  }

  .content {
    @include flex-all-center;
    flex-direction: column;
    position: relative;
    row-gap: 32px;
    background-color: var(--clr-accent-10);
    padding: var(--section-offset) 0;
    height: 742px;
    border-radius: var(--border-radius-md);
    overflow: hidden;

    .text-content {
      width: 56%;
      margin: 0 auto;
    }

    .text-content:has(.btn_success) {
      text-align: center;
    }
  }

  .progress-bar {
    position: absolute;
    top: 0;
    height: 16px;
    width: 100%;
    background-color: var(--clr-accent-10);
    transition: 0.3s;

		&__inner {
      width: 0;
      height: 100%;
      background-color: var(--clr-accent);
      transition: 0.3s;
		}
}

  .text, .first-heading {
    max-width: 840px;
  }

  .btn {
    margin-top: 32px;
  }

  .test {
		&__list {
      margin-top: 32px;
      
      & > * + * {
        margin-top: 12px;
      }
		}

    &__item {
      label {
        @include flex-v-center;
        column-gap: 24px;
        cursor: pointer;
      }
    }
  }

  .checkbox {
    appearance: none;
    width: 12px;
    height: 12px;
    border-radius: var(--border-radius-md);
    outline: 3px solid var(--clr-accent);
    outline-offset: 3px;

    cursor: pointer;

    &:checked {
      background-color: var(--clr-accent);
    }
  }

}


