.form {
  @include flex-column;
  flex-wrap: wrap;
  width: 660px;
  row-gap: 48px;
}

.input-group {
  @include flex-column;
  row-gap: 16px;
  flex: 1 1 50%;
  max-width: 100%;
}

.input-row {
  @include flex-v-center;
  column-gap: 24px;
  max-width: 100%;
}

.input-label {
  @include text(21px, var(--clr-dark), 114%, 700)
}

.input-label.error {
  color: var(--clr-danger);
}

.input, .select {
  @include text(21px, var(--clr-dark-75), 114%, 500);
  padding: 24px;
  border: 4px solid var(--clr-dark-2);
  border-radius: var(--border-radius-sm);
  &:focus {
    outline: var(--border-accent);
  }
}

.input-group {
  .choices__inner {
    @include text(21px, var(--clr-dark-75), 114%, 500);
    padding: 20px !important; 
    border: 4px solid var(--clr-dark-2);
    border-radius: var(--border-radius-sm);
    background-color: transparent;
    &:focus {
      outline: var(--border-accent) !important;
    }

}
}


.input {
  &::placeholder {
    @include text(21px, var(--clr-dark-50), 171%, 500)
  }
}

.input.error {
  outline: var(--border-error);
}

.input-group{
  position: relative;
}

.just-validate-error-label {
  position: absolute;
  top: 105%;
  left: 0;
}