.section-resume {
  .img {
    width: 160px;
    height: 160px;
    border-radius: var(--border-light-sm);
  }
  
  .fifth-heading {
    color: var(--clr-accent);
    margin-top: 8px;
  }

  .head {
    @include flex-v-center;
    column-gap: 32px;
  }

  .content {
    margin-top: var(--content-offset);
    display: flex;    
    column-gap: 48px;
    height: 100%;
  }

  .col {
    @include flex-column;
    row-gap: 48px;
    flex-basis: 55%;

  }

  .col:last-child {
    flex: 1;
  }


  .content .col > * {
    border: var(--border-light);
    padding: 64px;
    border-radius: var(--border-radius-md);
    flex-grow: 1;
  }

}



.resume {
	&__list {
    margin-top: 32px;

    & > * + * {
      margin-top: 12px;
    }
	}

  &__skills, &__projects {
    .text-content {
      @include flex-v-center;
      column-gap: 16px;
    }
  }
}



