.section-hero {
  .container {
    padding: 64px;
    background-color: var(--clr-dark);
    border-radius: 16px 800px 16px 16px;
  }
}

.main-imgs {
  position: relative;

  .main-img {
    @include hover {
      cursor: pointer;
      transform: scale(1.05);
    }
  }

  .main-icon {
    position: absolute;
    @include hover {
      cursor: pointer;
      transform: scale(1.1);
    }

    &-1 {
      left: 5%;
      top: -20%;
    }
    &-2 {
      right: -6%;
      top: -28%;
    }
    &-3 {
      right: -23%;
      bottom: 42%;
    }
  }
}

.cta {
  max-width: 563px;

  & > * + * {
    margin-top: 48px;
  }
}



