.success__overlay {
  position: fixed;
  z-index: 100;
  inset: 0;
  background-color:rgba(0, 20, 32, 0.25);
  height: 100vh;
  min-width: 100%;
  opacity: 0;
  transition: 0.5s;
  left: -1000%;
  visibility: hidden;
}

.success__overlay.active {
  left: 0;
  opacity: 1;
  visibility: visible;
  transition: 0.3s;
  overflow: hidden;
}

.success__modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  width: 56%;
  padding: 96px;
  border-radius: var(--border-radius-md);
  background-color: var(--clr-dark-2);

  .second-heading {
    text-align: center;
    color: var(--clr-dark);
  }
}

.success__modal.success {
  background-color: #ebfaec;
  .second-heading {
    color: var(--clr-success);
  }
}

.success__modal.error {
  background-color: #ffeded;
  .second-heading {
    color: var(--clr-danger);
  }
}