.section-contact-hero {
  .flex-container {
    align-items: flex-start;
  }
}

.contacts {
  max-width: 600px;

		&__list {
      @include flex-v-center;
      margin-top: 48px;
      gap: 32px;
		}

		&__item {
      @include hover {
        transform: scale(1.03);
      }
		}

  .text {
    margin-top: 48px;
  }
}

@include respond(desktop) {
  .flex-container {
    flex-wrap: wrap;
    row-gap: 64px;
  }

  .contacts {
    max-width: 100%;

    &__list {
      flex-wrap: wrap;
		}
  }

  .form {
    width: 100%;
    row-gap: 40px;
    
    .input-row {
      flex-wrap: wrap;
      row-gap: 40px;
    }
  }
}

@include respond(medium) {
  .contacts__item:last-child{
    min-width: 100%;
  }
}

@include respond(mobile) {
  // .form {
  //   max-width: 320px;
  // }
  
  .contacts__link {
    min-width: 100%;
  }
}