.section-program {
  .top {
    @include flex-all-sb;
  }

  .modules {
    margin-top: var(--content-offset);

    & > * + * {
      margin-top: 48px;
    }
  }

  .module {
    position: relative;
    @include flex-all-sb;
    
    padding: 64px 80px;
    border: var(--border);
    border-radius: var(--border-radius-md);

    .descr {
      max-height: 0;
      will-change: max-height;
      opacity: 0;
      overflow: hidden;
      box-sizing: content-box;
      transition: all 0.3s ease-out;
      

      &__list {
        margin-top: 24px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 12px 64px;
      }  
    }


  }

  
  .module.hidden {
    display: none;
  }

  .module.active  {
    .descr {
      opacity: 1;
      max-height: 100%;
      margin-top: 48px;
    }

    .plus {
      transform: rotate(45deg);
      transition: all 0.3s ease-out;
    }
  }

  .text {
    margin-top: 8px;
  }  

  .plus {
    position: absolute;
    top: 85px;
    right: 80px;
    transition: all 0.3s ease-out;

    svg {
      width: 48px;
      height: 48px;
    }
  }
}
