.section-faq {
  .content {
    margin-top: var(--content-offset);
    @include flex-all-sb;
    gap: 48px;
  }
}

.accordion {
  cursor: pointer;

  &__control {
    @include flex-v-center;
    column-gap: 24px;
  }

  &__list {
    max-width: 800px;

    & > * + * {
      margin-top: 32px;
    }
  }

  &__content {
    max-height: 0;
    will-change: max-height;
    opacity: 0;
    overflow: hidden;
    box-sizing: content-box;
    margin-top: 16px;
    transition: all 0.3s ease-out;
  }

  img {
    transition: 0.3s;
  }
}

.accordion.active {
  align-items: flex-start;
  

  .accordion__content {
    opacity: 1;
  }

  img {
    transform: rotate(45deg);
    transition: 0.3s;
  }
}

@include respond(desktop) {
 .section-faq {
  .content {
    flex-wrap: wrap;
    justify-content: center;
  }
 }

  .fifth-heading {
    text-align: left;
  }

}


@include respond(mobile) {
  .section-faq .faq-img {
    display: none;
  }
}
