html {
  position:relative; 
  min-height: 100%;
}

body {
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  min-height: 100%;
  overflow-x: hidden;
}

.container {
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  margin: 0 auto;
}

.container-md {
  max-width: 1440px;
}

.flex-container {
  @include flex-all-sb;
  gap: 32px;
}

.section {
  padding-top: var(--section-offset);
}

.main {
  margin-bottom: 430px;
}

.link_colored {
  @include text(21px, var(--clr-dark-75), 171%, 500);
  @include gradient-text;
  @include hover {
    filter: brightness(85%);
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.stop-scroll {
  overflow: hidden;
}

.just-validate-error-label {
  color: var( --clr-danger) !important;
  font-size: 17px;
}

@include respond(tablet) {
  :root {
    --section-offset: 80px;
    --content-offset: 48px;
  }
}

@import 'components/header';
@import 'components/btns';
@import 'components/typography';
@import 'components/social';
@import 'components/media';
@import 'components/form';
@import 'components/course';
@import 'components/course-page';
@import 'components/modal';
@import 'components/success';
@import 'components/footer';

