.section-course-info {
  .cards {
    // display: grid;
    // grid-template-columns: repeat(4, minmax(384px, 1fr));
    // grid-template-rows: repeat(2, 320px);
    display: flex;
    flex-wrap: wrap;
    gap: 48px;
  }

  .card {
    flex: 1 1 calc(25% - 48px);

    &:nth-child(n + 5) {
      flex-basis: calc(33% - 48px); 
    }
    @include flex-all-center;
    @include hover {
      transform: scale(1.02);
    }
    
    padding: 100px;
    margin: 0;
    background-color: var(--clr-accent-10);
    border-radius: var(--border-radius-md);
    cursor: pointer;
  }
  
  .third-heading {
    color: var(--clr-accent);
  }
}

