@each $key, $value in $headers-range {
  $size: map-get($value, size);
  $color: map-get($value, color);
  $lh: map-get($value, line-height);

  .#{$key}-heading {
    @include text($size, $color, $lh);
  }
}


.text {
  @include text(21px, var(--clr-dark-75), 171%, 500);

  &-center {
    text-align: center;
    margin: 0 auto;
  }
  
  &-light {
    color: var(--clr-light);
  }
  
  &-dark {
    color: var(--clr-dark);
  }

  &-success {
    color: var(--clr-success);
  }
}

@include respond(medium) {
  .text {
    font-size: 18px;
  }

  .first-heading {
    font-size: 60px;
  }

  .second-heading {
    font-size: 42px;
  }
  
  .third-heading {
    font-size: 32px;
  }
  
  .fourth-heading {
    font-size: 24px;
  }
  
  .fifth-heading {
    font-size: 20px;
  }
  
  .sixth-heading {
    font-size: 18px
  }
}