.section-procces {
  .third-heading {
    background-color: var(--clr-accent);
    padding: 64px;
  }

  .proceses {
    margin-top: var(--content-offset);
    & > * + * {
      margin-top: 32px;
    }
  }

  .procces {
    @include flex-all-sb;
    @include hover {
      transform: scale(1.01);
      cursor: pointer;
    }
    column-gap: 64px;
    background-color: var(--clr-dark-2);
    border-radius: var(--border-radius-md);
    overflow: hidden;
  }
}

