.section-salary {
  .content {
    margin-top: var(--content-offset);
    display: grid;
    grid-template-columns: repeat(2, minmax(700px, 1fr));
    grid-template-rows: minmax(380px, 1fr);
    gap: 48px;
  }
}

.salary {

  &__fourth-heading {
    color: var(--clr-accent);
  }

	&__info {
    border-radius: var(--border-radius-sm);
    background-color: var(--clr-accent-10);
    padding: 64px;

    .text {
      margin-top: 32px;
    }

    .text-content {
      margin-top: 32px;
      @include flex-all-sb;
    }
	}

	&__slider {
    padding: 64px;
    max-width: 680px;

    .input-range {
      margin-top: 48px;
      width: 100%;
      appearance: none;
      height: 16px;
      background-color: var(--clr-accent-10);
      border-radius: var(--border-radius-md);

      &::-webkit-slider-thumb  {
        appearance: none;
        width: 16px;
        height: 16px;
        background-color: var(--clr-accent-10);
        border-radius: var(--border-radius-md);
        outline: 8px solid var(--clr-accent);
        cursor: pointer;
      }
      
    }

    .text {
      margin-top: 8px;
    }
	}
  
}


.labels {
  margin-top: 16px;
  @include flex-all-sb;
}

